import React from 'react';
import './App.css';
import Product from './product'
import Total from './total'


let cproductList =  [ { "id": 9090, "name": "Item1", "price": 200, "discount": 10, "type": "fiction", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9091, "name": "Item2", "price": 250, "discount": 15, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9092, "name": "Item3", "price": 320, "discount": 5, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9093, "name": "Item4", "price": 290, "discount": 0, "type": "thriller", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9094, "name": "Item5", "price": 500, "discount": 25, "type": "thriller", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9095, "name": "Item6", "price": 150, "discount": 5, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9096, "name": "Item7", "price": 700, "discount": 22, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9097, "name": "Item8", "price": 350, "discount": 18, "type": "fiction", "img_url": "https://place-hold.it/40.jpg" } ];
class ProductList extends React.Component {
    constructor(props) {
      super(props);
  
      this.state = {
        total: 0,
        actual:0,
        discount: 0,
        typediscount:0,
        productList: [ { "id": 9090, "name": "Item1", "price": 200, "discount": 10, "type": "fiction", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9091, "name": "Item2", "price": 250, "discount": 15, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9092, "name": "Item3", "price": 320, "discount": 5, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9093, "name": "Item4", "price": 290, "discount": 0, "type": "thriller", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9094, "name": "Item5", "price": 500, "discount": 25, "type": "thriller", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9095, "name": "Item6", "price": 150, "discount": 5, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9096, "name": "Item7", "price": 700, "discount": 22, "type": "literature", "img_url": "https://place-hold.it/40.jpg" }, { "id": 9097, "name": "Item8", "price": 350, "discount": 18, "type": "fiction", "img_url": "https://place-hold.it/40.jpg" } ]
      };
  
      this.calculateTotal = this.calculateTotal.bind(this);
      this.onRemoveProduct = this.onRemoveProduct.bind(this);
      this.add = this.add.bind(this);
      this.subtract = this.subtract.bind(this);
      this.onReload = this.onReload.bind(this);
      this.componentDidMount = this.componentDidMount.bind(this);
    }
    
    componentDidMount(){
      let productList = cproductList;
      let total=0;
      let discount = 0;
      let typediscount= 0;
      for(var i=0;i<productList.length;i++){
            productList[i].qty = 1;
            cproductList[i].qty = 1;
            total+=productList[i].price;
            cproductList[i].typediscount = (cproductList[i].type==="fiction")? (0.15*cproductList[i].price):0;
            productList[i].typediscount = (productList[i].type==="fiction")? (0.15*productList[i].price):0;
            discount+=(productList[i].discount/100)*productList[i].price;
            typediscount+=productList[i].typediscount;
      }
      console.log("initial total = "+total+"initial discount = "+discount);
      this.setState({productList:productList, actual: total, total:total, discount:discount, typediscount: typediscount});
    }
    add(idx) {
      let productList = this.state.productList;
      console.log(productList[idx].discount);
     productList[idx].qty++;
     console.log(productList[idx].price);
      this.setState({
        productList: productList
      });
      this.calculateTotal(productList[idx].price, productList[idx].discount, productList[idx].typediscount);
    }
  
    subtract(idx) {
        if(this.state.productList[idx].qty>0){
          let productList = this.state.productList;
          productList[idx].qty--;
      this.setState({
        productList: productList
      });
      this.calculateTotal(-productList[idx].price, -productList[idx].discount, -productList[idx].typediscount);
    }
    }
    onReload =()=> {
         this.componentDidMount()
         console.log(this.state.productList);
    }
    onRemoveProduct(id) {
        console.log(id);
        this.setState(prevstate => ({
          productList: prevstate.productList.filter(item=>item.id!==id)
        }));
        /*const products = this.state.productList.filter((item) => item.id !== id);
    
        this.setState({ productList: products });*/
        console.log(this.state.productList.length); 
      };
  
    calculateTotal(price, discount, typediscount) {
      //console.log(price+" "+discount);
      let total = this.state.total + price - (discount/100)*price -typediscount;
      let actual = this.state.actual + price;
      console.log("total= "+total);
      this.setState({
        total: total,
        actual:actual,
        discount: this.state.discount + (discount/100)*price,
        typediscount: this.state.typediscount + typediscount
      });
     // console.log(this.state.total);
    }
  
    render() {  
      var component = this;
      const p = this.state.productList;
      if(p.length===0) return(
          <div className="container" onClick={this.onReload.bind(this)}><button>Reload</button></div>
      )
      var products =  this.state.productList.map(function(product,index) {
        return (
        <div className="container">            
          <Product
            name={product.name}
            img_url = {product.img_url}
            key={product.id}
            id = {product.id}
            price={product.price}
            type = {product.type}
            qty = {product.qty}
            add = {component.add}
            index = {index}
            subtract = {component.subtract}
            discount = {product.discount}
            onRemoveProduct={component.onRemoveProduct}
            handleTotal={component.calculateTotal}
          />
          </div>
        );
      });
  
      return (
        <React.Fragment>
          <div className="d-inline-flex flex-row">
            <div className="p-2">
            <table className="table borderless">
              <div className="container">
            <thead>
                    <tr>
                        <th className = "item">Items</th>
                        <th className="qty">Qty</th>
                        <th>Price</th>
                    </tr>
            </thead>
            </div>
          <div className="p-2">
              <tbody>
                <tr>
                   {products}
                </tr>
              </tbody>
          </div>
            </table>
            </div>
          <div className="p-2 m-2">
          <Total total={this.state.total} count = {this.state.productList.length} actual={this.state.actual} discount = {this.state.discount} typediscount = {this.state.typediscount}/>
          </div>
          </div>
        </React.Fragment>
      );
    }
  }

  export default ProductList;